import React from 'react';
import { Row, Col } from '../UI/Grid';
import { siteUrl } from '../../../config.json';

function enc(str) {
  return encodeURIComponent(str);
}

function getFullLink(path) {
  return enc(`${siteUrl}${path}`);
}

export default function Share(props) {
  const { post, color } = props;
  return (
    <Row className="share">
      <Col>
        <a className="i-light fa fa-facebook fa-4x" style={{ color }} href={`https://www.facebook.com/sharer/sharer.php?app_id=778520525558698&u=${enc(post.permalink)}&t=${enc(post.title)}&display=popup&ref=plugin&src=potofcode`} target="fb" />
      </Col>
      <Col>
        <a className="i-light fa fa-vk fa-4x" style={{ color }} href={`http://vk.com/share.php?url=${enc(post.permalink)}&title=${enc(post.title)}&image=${getFullLink(post.images.share.src)}`} target="vk" />
      </Col>
      <Col>
        <a className="i-light fa fa-twitter fa-4x" style={{ color }} href={`https://twitter.com/intent/tweet?url=${enc(post.permalink)}&text=${enc(post.title)}&via=potofcode&hashtags=potofcode`} target="tw" />
      </Col>
      <Col>
        <a className="i-light fa fa-google-plus fa-4x" style={{ color }} href={`https://plus.google.com/share?url=${enc(post.permalink)}`} target="gp" />
      </Col>
    </Row>
  );
}



